import {
	Toast
} from 'vant'
import {
	ajax
} from '../../common/js/util.js'
export default {
	data() {
		return {
			rTime: '获取验证码',
			tel: '',
			code: '',
			btnState: false,
			uid: null
		}
	},
	components: {
		Toast
	},
	onShow() {
		if (this.getRequest('uid')) {
			var uids = this.getRequest('uid')
			this.$store.commit('changeUid', uids)
			this.uid = uids
			if (this.getRequest('bind') && this.getRequest('type')) {
				var bind = this.getRequest('bind')
				var type = this.getRequest('type')
				if (bind == 1 && type == 1) {
					uni.switchTab({
						url: '/pages/vip/vip'
					})
				} else if (bind == 1 && type == 2) {
					uni.switchTab({
						url: '/pages/no/no'
					})
				}
			}
		}
	},
	methods: {
		getRequest(variable) {
			var query = window.location.search.substring(1);
			var vars = query.split("&");
			for (var i = 0; i < vars.length; i++) {
				var pair = vars[i].split("=");
				if (pair[0] == variable) {
					return pair[1];
				}
			}
			return (false);
		},
		getValidate() {
			if (this.tel == '') {
				Toast('手机号不能为空')
				return
			} else if (!(/^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/.test(this.tel))) {
				Toast('手机号格式不正确')
				return
			}

			let self = this
			this.rTime = 60
			let rTime = this.rTime
			this.btnState = true
			let interval = setInterval(() => {
				if (--self.rTime <= 0) {
					self.rTime = '获取验证码'
					clearInterval(interval)
					self.btnState = false
				}
			}, 1000)

			let datas = {
				url: '/api/gzh/sendSms',
				data: {
					phone: self.tel
				},
				success: function(res) {
					console.log(res)
				}
			}

			ajax(datas)
		},
		submit() {
			let self = this
			if (this.tel == '') {
				Toast('手机号不能为空')
				return
			} else if (!(/^1[3456789]\d{9}$/.test(this.tel))) {
				Toast('手机号格式不正确')
				return
			}

			if (this.code == '') {
				Toast('验证码不能为空')
				return
			}
			let datas = {
				url: '/api/gzh/bindPhone',
				data: {
					uid: self.uid,
					phone: self.tel,
					code: self.code
				},
				success: function(res) {
					console.log(res)
					if (res.data.result == 0) {
						uni.switchTab({
							url: '/pages/index/index'
						})
					} else {
						Toast(res.data.resultNote)
					}
				}
			}
			ajax(datas)
		}
	}
}
